/* Mobile filter */
.mobile-category-filters {
    display: none;
    margin: 1em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.mobile-filter-options {
	padding-left: 30px;
    color: brown;
}
.mobile-categories {
	padding-left: 30px;
    color: brown;
}
.mobile-region {
	padding-left: 30px;
    color: brown;
}

.antartic-page-title {
