}
/* CARD STYLES */

.card {
	width: 120%;
	height: 36em;
	margin-left: 0em;
	margin-top: 0em;
	margin-bottom: 2em;
}

.card:hover {
	box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.254) !important;
}

.card-body {
	padding: 2rem 0 !important;
}

.card-text {
	margin: 0px;
	font-size: 13px;
	padding: 0.4rem 1.9rem;
}

.col {
	text-align: center;
	margin: 2px;
}

.container-fluid.row {
	padding-top: 0em;
}

.container-fluid {
	padding-top: 1em;
	margin-left: 12em;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.overflow {
	overflow: hidden;
}

.card-img-top {
	-webkit-transform: scale(1);
	        transform: scale(1);
	transition: -webkit-transform 0.5s ease;
	transition: transform 0.5s ease;
	transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

.card-img-top:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.card-body .btn {
	width: 10em;
}

.container-fluid .row {
	padding-right: 3em;
	margin-left: 0px;
	margin-top: -45em;
}

.allsites-loading {
	margin-top: -42em;
	margin-bottom: 30em;
	margin-left: 0em;
}
